import "./styles/Works.css";
import portfolio from "../imgs/screen-portffolio.png";
import visimusic from "../imgs/screen-visimusic.png";

const Works = () => {
  return (
    <div className="works" id="works">
      <p className="works__title">PROJECTS</p>

      <section className="works__gallery">

        <figure class="works__gallery-item works__gallery-item1">
          <a href="https://github.com/rlpedro/rlpedro.github.io/" target="_blank" rel="noreferrer">
            <img src={portfolio} class="gallery__img" alt="Ricardo Leitao Pedro's portfolio" />
          </a>
          <span className="works__gallery-item-title">Web Portfolio</span>
          <span className="works__gallery-item-desc">front-end for online portfolio</span>
          <span className="works__gallery-item-tech">Tech stack: ReactJS, NodeJS</span>
          <section className="works__gallery-item-buttons">
            <a href="https://github.com/rlpedro/rlpedro.github.io/" target="_blank" rel="noreferrer">
              <button className="gallery-button">CODE</button>
            </a>
          </section>
        </figure>

      <figure class="works__gallery-item works__gallery-item2">
        <a href="https://visimusic.herokuapp.com/" target="_blank" rel="noreferrer">
          <img src={visimusic} class="gallery__img" alt="Visimusic app" />
        </a>
        <span className="works__gallery-item-title">VisiMusic</span>
        <span className="works__gallery-item-desc">full-stack e-commerce app for musical instruments and equipment</span>
        <span className="works__gallery-item-tech">Tech stack: ReactJS, NodeJS, MongoDB</span>
        <section className="works__gallery-item-buttons">
          <a href="https://visimusic.herokuapp.com/" target="_blank" rel="noreferrer">
              <button className="gallery-button">APP</button>
          </a>
          {/* <a href="https://visimusic.herokuapp.com/" target="_blank"> */}
            <button className="gallery-button">CODE</button>
          {/* </a> */}
        </section>
      </figure>

        <figure class="works__gallery-item works__gallery-item3">
          {/* <img src="img/image-3.jpg" class="gallery__img" alt="Project 3" /> */}
          <span>Project 3</span>
        </figure>
        <figure class="works__gallery-item works__gallery-item4">
          {/* <img src="img/image-4.jpg" class="gallery__img" alt="Project 4" /> */}
          <span>Project 4</span>
        </figure>
        <figure class="works__gallery-item works__gallery-item5">
          {/* <img src="img/image-5.jpg" class="gallery__img" alt="Project 5" /> */}
          <span>Project 5</span>
        </figure>
        <figure class="works__gallery-item works__gallery-item6">
          {/* <img src="img/image-6.jpg" class="gallery__img" alt="Project 6" /> */}
          <span>Project 6</span>
        </figure>
      </section>
    </div>
  );
};

export default Works;
